import { Content } from "./content";

export class List {
  constructor(listEl, list) {
    this.list = list;
    this.listEl = listEl;
    this.contentEl = document.querySelector("#content");
    this.content = new Content(this.contentEl); // —Å–æ–∑–¥–∞–µ–º —ç–∑–µ–º–ø–ª—è—Ä –∫–ª–∞—Å—Å–∞ Content (content.js)
    this.init();
  }

  init() {
    // –∑–∞–ø—É—Å–∫–∞–µ–º –º–µ—Ç–æ–¥ render
    this.render();
  }

  render() {
    this.listEl.innerHTML = "";

    // –ø—Ä–æ—Ö–æ–¥–∏–º—Å—è –ø–æ list (data.json) –∏ –≤—Å—Ç–∞–≤–ª—è–µ–º –∑–Ω–∞—á–µ–Ω–∏—è title –∏ id –≤ —Å–æ–∑–¥–∞–≤–∞–µ–º—ã–µ li
    this.list.forEach((item, index) => {
      const liEl = this.getListItem(item, index);
      this.listEl.append(liEl); // –∏—Å–ø–æ–ª—å–∑—É–µ–º append –¥–ª—è –¥–æ–±–∞–≤–ª–µ–Ω–∏—è —ç–ª–µ–º–µ–Ω—Ç–∞ —Å–æ–∑–¥–∞–Ω–Ω–æ–≥–æ –¥–∏–Ω–∞–º–∏—á–µ—Å–∫–∏
    });
  }

  // –§–æ—Ä–º–∞—Ç–∏—Ä—É–µ–º id (–∏–∑ new Date) –∫ –≤–∏–¥—É "10.01.2021, 23:25:27"
  getFormatedDate(id) {
    return new Date(id).toLocaleString();
  }

  getListItem(item, index) {
    const liEl = document.createElement("li");

    liEl.classList = "main-section__item";
    liEl.setAttribute("data-id", item.id);
    liEl.innerHTML = `
      ${item.title}
      <time class="main-section__time">${this.getFormatedDate(+item.id)}</time>
    `;

    // –ø–æ –∫–ª–∏–∫—É –≤—ã–∑–∞–≤–∞–µ–º –º–µ—Ç–æ–¥ render(item) - –æ—Ç—Ä–∏—Å–æ–≤—ã–≤–∞–µ—Ç—Å—è content —Å–æ–æ—Ç–≤–µ—Ç—Å—Ç–≤—É—é—â–∏–π li
    liEl.addEventListener("click", () => {
      this.onListItemClick(liEl, item);
    });

    if (index === 0 && this.list.length > 0) {
      liEl.classList.add("main-section__item--active");
      this.content.render(item);
    }

    return liEl;
  }

  onListItemClick(liEl, item) {
    // –∑–∞–ø–∏—Å—ã–≤–∞–µ–º –≤ –º–∞—Å—Å–∏–≤ –≤—Å–µ li (–¥–æ —ç—Ç–æ–≥–æ –±—ã–ª –ø—Å–µ–≤–¥–æ–º–∞—Å—Å–∏–≤ - html –∫–æ–ª–µ–∫—Ü–∏—è)
    const items = Array.from(document.querySelectorAll(".main-section__item"));

    // —É–¥–∞–ª—è–µ–º –∫–ª–∞—Å—Å active
    items.forEach((elem) => {
      elem.classList.remove("main-section__item--active");
    });

    // –¥–æ–±–∞–≤–ª—è–µ–º –∫–ª–∞—Å—Å active
    liEl.classList.add("main-section__item--active");
    this.content.render(item);
  }
}
